<template>
  <div>
    <el-row style="margin-bottom:20px;">
      <el-card shadow="always">
        <span class="content-title">案例咨询管理</span>
      </el-card>
    </el-row>
    <el-table
      border
      :data="tableData"
    >
      <el-table-column
        prop="index"
        label="序号"
        align="center"
        width="80"
      >
      </el-table-column>
      <el-table-column
        prop="title"
        label="案例标题"
        align="center"
        width="130"
      >
      </el-table-column>
      <el-table-column
        prop="company"
        label="公司名称"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        align="center"
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="email"
        label="邮箱"
        align="center"
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="create_time"
        label="创建时间"
        align="center"
        width="200"
      >
      </el-table-column>
    </el-table>
    <div class="block pager">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[15, 20, 30]"
        :page-size="page_size"
        layout="sizes, prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { utcTimeChange } from "@/utils";
export default {
  data() {
    return {
      selectClass: "el-icon-arrow-down",
      //表格
      tableData: [],
      currentPage: 1,
      page_size: 15,
      total: 0,
    };
  },
  mounted() {
    this.getInfoList("1", "15");
  },
  methods: {
    handleSizeChange(val) {
      this.getInfoList(this.currentPage, val);
    },
    handleCurrentChange(val) {
      this.getInfoList(val, this.page_size);
    },
    getInfoList(page_index, page_size) {
      var params = { page_index: page_index, page_size: page_size };
      var that = this;
      axios.get("app/customer/pageList", { params }).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            var array = [];
            var rdata = response.data;
            var count = rdata.count;
            var page_index = rdata.page_index;
            var page_size = rdata.page_size;
            that.currentPage = page_index;
            that.page_size = page_size;
            that.total = count;
            var des_num = (page_index - 1) * page_size;
            var json = rdata.rows;
            for (var i = 0; i < json.length; i++) {
              json[i].index = des_num + i + 1;
              json[i].create_time = utcTimeChange(json[i].create_time);
              array.push(json[i]);
            }
            that.tableData = array;
          } else {
          }
        },
        function(error) {
        }
      );
    },
  }
};
</script>

<style scoped>
.content-title {
  margin-right: 20px;
}
.advanced-box {
  padding: 20px 10px;
  margin-top: 15px;
  background-color: #f7f7f7;
}
.pager {
  float: right;
  padding: 10px 0;
}
.el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.el-dialog-counsel {
  height: 530px !important;
  padding-right: 15px;
  overflow-y: scroll;
}
.el-dialog__footer {
  border-top: 1px solid #ccc;
}
.el-form-item__label {
  padding-bottom: 0 !important;
}
</style>



